import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { InputGroupInline, InputType, REFERENCE, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { MATRICULA, ORDENACAO_TRABALHADOR_ENUM_VALUES } from '../../../../../models/enumerated/mfp/ordenacaoTrabalhorEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import CentroCustoIS from '../../../../components/mcg/centroCustoIS/CentroCustoIS';
import TomadorServicoIS from '../../../../components/mfp/tomadorServicoIS/TomadorServicoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, HorarioTrabalhoRV, REPORT_EVENT, REPORT_TITLE } from './horarioTrabalhoRV';

const DEFAULT_VALUES: HorarioTrabalhoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: false,
  refInicialLabel: 'Referência',
  refInicialRequired: true,
};

const HorarioTrabalhoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<HorarioTrabalhoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  useEffect(() => {
    setTimeout(() => {
      formProps.setValue('ordenacao', MATRICULA.value, true);
      formProps.setValue('referenciaInicial', globalParameter.empresaMFP.referencia);
      formProps.setValue('tomadoresSelecionados', []);
      formProps.setValue('vinculosSelecionados', []);
    }, 200);
  });

  return (
    <WindowComponent
      title="Horário de Trabalho"
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter), () => hasGrupoEmpresaConfig(globalParameter)]}
      wikiId={3785}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <Row>
          <Col>
            <VinculoIS
              id="vinculosSelecionados"
              formProps={formProps}
              label="Funcionários"
              placeholder="Selecione os Funcionários"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Se nenhum funcionário for selecionado todos serão utilizados.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <TomadorServicoIS
              id="tomadoresSelecionados"
              formProps={formProps}
              label="Tomadores de Serviço"
              placeholder="Selecione os Tomadores"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Se nenhum tomador for selecionado todos serão utilizados.' }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CentroCustoIS id="centroCusto" formProps={formProps} label="Centro de Custo" inputSearchProps={{ selectProps: { isMulti: true } }} />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline id="observacao" label="Observação" isPrependLabel={true} formProps={formProps} type={InputType.TEXT_AREA} />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="ordenacao"
              label="Ordem"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: ORDENACAO_TRABALHADOR_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <br />
      </ReportForm>
    </WindowComponent>
  );
};

export default HorarioTrabalhoReportView;
